import {Injectable} from '@angular/core';
import {MyClappService, PictureListModel, ProductModel} from '@myclapp/myclapp-server';
import {BehaviorSubject, Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Logger} from '@upe/logger'


import {BaseSearchService} from './base.search.service';
import {UserService} from './user.service';

@Injectable({providedIn: 'root'})
export class ProductsService extends BaseSearchService<ProductModel> {

    private logger: Logger = new Logger({name: ProductsService.name, flags: ['service']});

    searchPropertyName = 'name' as keyof ProductModel;

    refreshRequired$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

    constructor(private apiClient: MyClappService, private userService: UserService) {
        super();
    }

    getResults(): Observable<ProductModel[]> {
        return this.apiClient.getProducts({
            clubId: this.userService.club_id,
        }).pipe(map(res => {
            this.logger.debug('Received Results', res)
            return res.items;
        }));
    }

    public addProduct(product: any): Observable<ProductModel> {
        this.logger.debug('Adding new Product', product)
        return this.apiClient.createProduct({
            clubId: this.userService.club_id,
            body: {
                active: true,
                description: product.description,
                name: product.name,
                price: product.price,
                picture: product.picture
            },
        }).pipe(map(model => {
            this.logger.info('Added new Product', model)
            this.clearCacheAndSearch();
            this.refreshRequired$.next(true);
            return model;
        }));
    }

    public editProduct(product: any): Observable<ProductModel> {
        return this.apiClient.updateProduct(
            {
                clubId: this.userService.club_id,
                productId: product.id,
                body: {
                    active: true,
                    description: product.description,
                    name: product.name,
                    price: product.price,
                    picture: product.picture
                }
            }
        ).pipe(map(model => {
            this.logger.debug('Product updated', model);
            this.clearCacheAndSearch();
            this.refreshRequired$.next(true);
            return model;
        }));
    }

    public getProduct(id: number): Observable<ProductModel> {
        return this.apiClient.getProduct({
            clubId: this.userService.club_id,
            productId: id
        }).pipe(map(model => {
            this.logger.debug('Fetched Product', model)
            return model
        }))
    }

    public deleteProduct(id: number): Observable<ProductModel> {
        return this.apiClient.deleteProduct({
                clubId: this.userService.club_id,
                productId: id
            }
        ).pipe(map(
            model => {
                this.clearCacheAndSearch();
                this.refreshRequired$.next(true)
                this.logger.debug('Deleted Product', model)
                return model
            }
        ))

    }

    public getPictureForTerm(term: string): Observable<PictureListModel> {
        return this.apiClient.searchPictures({
            query: term,
        })
    }

    public getPictureById(id: number) {
        return this.apiClient.getPicture({
            pictureId: id
        })
    }

    public uploadCustomPicture() {

    }
}
