import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {MyClappService, UserModel} from '@myclapp/myclapp-server';
import {BehaviorSubject, Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Logger} from '@upe/logger'


@Injectable({providedIn: 'root'})
export class UserService {
    private currentUserSubject: BehaviorSubject<UserModel>;
    private logger: Logger = new Logger({name: UserService.name, flags: ['service']});

    constructor(private myClappService: MyClappService, private router: Router) {
        this.currentUserSubject = new BehaviorSubject<UserModel>(JSON.parse(localStorage.getItem('currentUser') || '{}'));
    }

    get user(): UserModel {
        return this.currentUserSubject?.value;
    }

    set user(user: UserModel) {
        this.currentUserSubject.next(user);
    }

    get club_id(): number {
        return this.currentUserSubject?.value.club_id as number;
    }

    get club_name():string {
        return this.currentUserSubject?.value.club_name as string;
    }


    get user$(): Observable<UserModel> {
        return this.currentUserSubject.asObservable();
    }


    login(email: string, password: string) {
        this.logger.debug('Attempting login')
        return this.myClappService.login({ body: { email, password } }).pipe(map(user => {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            this.logger.debug('Successful login',user)
            localStorage.setItem('currentUser', JSON.stringify(user));
            this.currentUserSubject.next(user);
            return user;
        }));
    }

    logout() {
        this.logger.debug('Attempting log out')
        localStorage.removeItem('currentUser');
        this.currentUserSubject.next({} as any);
        this.router.navigate(['/account/login'])
    }

    isAuthenticated(): boolean {
        return !(this.currentUserSubject.value.token == undefined || this.currentUserSubject.value.token == '');
    }
}
