import { Injectable } from '@angular/core';
import { Alert } from '@app/modules/app-common/components';
import {BehaviorSubject} from 'rxjs';
import {Logger} from '@upe/logger'

@Injectable({ providedIn: 'root' })
export class AlertService {
    private _alerts = new BehaviorSubject<Alert[]>([]);
    private logger: Logger = new Logger({ name: AlertService.name, flags: ["service"] });


    show(alert: Alert) {
        this.logger.debug('Display new alert', alert)
        this._alerts?.value.push((alert));
        this._alerts.next(this._alerts?.value);
    }

    remove(alert: Alert) {
        this._alerts.next(this._alerts?.value.filter(a => a !== alert));
    }

    alerts$() {
        return this._alerts.asObservable();
    }

    success(text: string) {
        this.show({
            message: text,
            type: 'success',
        });
    }

    info(text: string) {
        this.show({
            message: text,
            type: 'info',
        });
    }

    danger(text: string) {
        this.show({
            message: text,
            type: 'danger',
        });
    }
}
