import { Injectable } from '@angular/core';
import { MyClappService, PushMessageModel } from '@myclapp/myclapp-server';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import{Logger } from '@upe/logger'


import { BaseSearchService } from './base.search.service';
import { UserService } from './user.service';


@Injectable({ providedIn: 'root' })
export class PushService extends BaseSearchService<PushMessageModel> {

    searchPropertyName = 'title' as keyof PushMessageModel;
    private logger: Logger = new Logger({ name: PushService.name, flags: ["service"] });

    constructor(private apiClient: MyClappService, private userService: UserService) {
        super();
    }

    getResults(): Observable<PushMessageModel[]> {
        this.logger.debug('Query PushMessages')
        return this.apiClient.getPushMessages({
            clubId: this.userService.club_id,
        }).pipe(map(res => {
            this.logger.debug('Received PushMessages', res)
            return res.items;
        }));
    }

    public sendPush(): any {
        this.logger.debug('Creating Push Notification (currently static)')
        return this.apiClient.createPushMessage({
            clubId: this.userService.club_id,
            body: {
                content: 'Inhalt',
                title: 'Titel',
            },
        }).subscribe(() => {
            this.clearCacheAndSearch();
        });
    }
}
