import { DecimalPipe } from '@angular/common';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { apiConfigFactory } from '@app/api.config';
import {SBSortableHeaderDirective} from '@app/directives/sortable.directive';
import {AuthGuard} from '@app/modules/auth/guards';
import {environment} from '@environments/environment';
import { BASE_PATH, MyClappApiModule } from '@myclapp/myclapp-server';

import { ErrorInterceptor } from '../helpers/error.interceptor';
import { fakeBackendProvider } from '../helpers/fake-backend';
import { JwtInterceptor } from '../helpers/jwt.interceptor';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

@NgModule({
    declarations: [AppComponent, SBSortableHeaderDirective],
    imports: [BrowserModule, AppRoutingModule, HttpClientModule,
        MyClappApiModule.forRoot(apiConfigFactory),
    ],
    providers: [
        DecimalPipe,
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
        {
            provide: BASE_PATH,
            useValue: environment.apiUrl,
        },
        // provider used to create fake backend
        fakeBackendProvider,
        AuthGuard
    ],
    bootstrap: [AppComponent],
})
export class AppModule {
}
console.log(environment.apiUrl)
