import { DecimalPipe } from '@angular/common';
import { Inject, Injectable, PipeTransform } from '@angular/core';
import { SortDirection } from '@app/directives/sortable.directive';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { debounceTime, delay, map, switchMap, tap } from 'rxjs/operators';


function compare(v1: number | string | boolean, v2: number | string | boolean) {
    return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

export function sort<T>(models: T[], column: keyof T, direction: string): T[] {
    if (direction === '') {
        return models;
    } else {
        return [...models].sort((a, b) => {
            if (['number', 'string', 'boolean'].includes(typeof a[column])) {
                const res = compare(a[column] as any, b[column] as any);
                return direction === 'asc' ? res : -res;
            }
            return 0;
        });
    }
}

export function matches<T>(push: T, property: keyof T, term: string, pipe: PipeTransform) {
    return ((push[property] as any) as string).toLowerCase().includes(term.toLowerCase());
}

export interface SearchResult<T> {
    items: T[];
    total: number;
}

export interface SearchState<T> {
    page: number;
    pageSize: number;
    searchTerm: string;
    sortColumn: keyof T | undefined;
    sortDirection: SortDirection;
}

@Injectable({ providedIn: 'root' })
export abstract class BaseSearchService<T> {
    abstract searchPropertyName: keyof T;
    private _search$ = new Subject<void>();
    private _cachedResults: T[] = [];
    private _state: SearchState<T> = {
        page: 1,
        pageSize: 4,
        searchTerm: '',
        sortColumn: undefined,
        sortDirection: '',
    };
    @Inject(DecimalPipe)
    private pipe: DecimalPipe | undefined;

    constructor() {
        this._search$.pipe(
            tap(() => this._loading$.next(true)),
            debounceTime(120),
            switchMap(() => this._search()),
            delay(120),
            tap(() => this._loading$.next(false)),
        ).subscribe(result => {
            this._results$.next(result.items);
            this._total$.next(result.total);
        });
        this._search$.next();
    }

    private _loading$ = new BehaviorSubject<boolean>(true);

    get loading$() {
        return this._loading$.asObservable();
    }

    private _results$ = new BehaviorSubject<T[]>([]);

    get results$() {
        return this._results$.asObservable();
    }

    private _total$ = new BehaviorSubject<number>(0);

    get total$() {
        return this._total$.asObservable();
    }

    get page() {
        return this._state.page;
    }

    set page(page: number) {
        this._set({ page });
    }

    get pageSize() {
        return this._state.pageSize;
    }

    set pageSize(pageSize: number) {
        this._set({ pageSize });
    }

    get searchTerm() {
        return this._state.searchTerm;
    }

    set searchTerm(searchTerm: string) {
        this._set({ searchTerm });
    }

    set sortColumn(sortColumn: keyof T) {
        this._set({ sortColumn });
    }

    set sortDirection(sortDirection: SortDirection) {
        this._set({ sortDirection });
    }

    public getCache(): Observable<T[]> | undefined {
        if (this._cachedResults.length > 0) {
            return of(this._cachedResults);
        }
        return undefined;
    }

    public clearCacheAndSearch() {
        this._cachedResults = [];
        this._search();

    }

    abstract getResults(): Observable<T[]>;

    private _set(patch: Partial<SearchState<T>>) {
        Object.assign(this._state, patch);
        this._search$.next();
    }

    private _search(): Observable<SearchResult<T>> {
        const cachedResults = this.getCache();
        let res;
        if (cachedResults) {
            res = cachedResults;
        } else {
            res = this.getResults();
        }

        return res.pipe(
            map(allResults => {
                this._cachedResults = allResults;

                const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;
                let items = allResults;

                if (sortColumn) {
                    items = sort(allResults, sortColumn, sortDirection);
                }

                items = items.filter(result => matches(result, this.searchPropertyName, searchTerm,
                    this.pipe as DecimalPipe));
                const total = items.length;

                items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);
                return { items, total };
            }),
        );
    }
}
