import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {Logger} from '@upe/logger'


@Injectable({providedIn: 'root'})
export abstract class BaseConfigService<T> {

    _logger: Logger;

    abstract _cachedConfig: T | T[] | null;

    constructor(name: string) {
        this._logger = new Logger({name: name, flags: ['service']});
    }

    abstract submit(data: T): Observable<T> | Observable<T[]>

    abstract _refresh(): Observable<T | T[]>

    abstract _getCachedConfig(): T | T[] | null

    abstract get(refresh?: true): Observable<T | T[]>


}
